import logo from './logo.svg';
import './App.css';
import API from './api';
import React, { useState, useEffect } from "react";

function App() {

  const [data, setData] = useState(null)
  const [loading, setLoading] = useState(false)
  let init = false;

  const getData = async () => {
    if(init) {
      return false;
    }
    init = true;
    setLoading(true);
    // const dicData = JSON.parse(tempJSON);

    const api = new API();
    const res = await api.getFixture();
    const dicData = JSON.stringify(res.data);
    setLoading(false);
    setData(dicData);
    console.log("data set!");

  };

  useEffect(() => {
    getData();
  }, [])

  return (
    <div className="App">
      <header className="App-header">
        <p>
          {JSON.stringify(data)}
        </p>
      </header>
    </div>
  );
}

